import React, { Component, Fragment } from 'react';

import CarCard from './CarCard';

import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import MobileStepper from '@material-ui/core/MobileStepper';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';

class CarCarousel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeStep: 0,
        };
    }

    handleNext = () => {
        this.setState({
            activeStep: this.state.activeStep + 1,
        });
        this.props.clickNext();
    };

    handleBack = () => {
        this.setState({
            activeStep: this.state.activeStep - 1,
        });
        this.props.clickBack();
    };

    handleRent =() =>{

    }
    render() {
        return (
            <div style={{
                position: "absolute",
                top: "6em",
                margin: "auto",
            }}>
                <div style={{
                    margin: "auto",
                }}>
                    {this.props.cars.map(
                        (tile, i) => (
                            (<CarCard
                                key={i}
                                carName={tile[2]}
                                plate={tile[0]}
                                transmission={tile[6]}
                                seats={tile[11]}
                                fuelType={tile[8]}
                                position={i}
                                currentCar={this.props.currentCar} 
                                clickRent={this.props.clickRent}/>
                            ))
                    )}
                </div>

                <MobileStepper
                    variant="dots"
                    steps={this.props.cars.length - 1}
                    position="static"
                    style={{
                        zIndex: "100",
                        position: "absolute",
                        top: "18em"
                    }}
                    activeStep={this.state.activeStep}
                    nextButton={
                        <Button size="small"
                            onClick={this.handleNext}
                            disabled={this.state.activeStep === this.props.cars.length - 1}>
                            Siguiente
                        <KeyboardArrowRight />
                        </Button>
                    }
                    backButton={
                        <Button size="small"
                            onClick={this.handleBack}
                            disabled={this.state.activeStep === 0}>
                            <KeyboardArrowLeft />
                            Anterior
                    </Button>
                    }
                />

            </div>
        )
    }
}

export default CarCarousel;


