import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
class DateSelect extends Component {
    constructor(props) {
        super(props);
        this.state = {
            startDate: '',
            number: '',

        };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        console.log(event.target)
        this.setState({ [event.target.id]: event.target.value });
    }

    render() {
        return (
            <Card style={{ margin: "3em", zIndex: "100", position: "absolute", top: "60px" }}>
                <Grid container>
                    <Grid item xs={12}>
                        Selecciona tu reserva
                        <Card style={{ margin: "1em" }}>
                            <Grid container>
                                <Grid item xs={12}>
                                    <img src="/car.png" height="75em" />
                                </Grid>
                                <Grid item xs={6}>
                                    Automatico
                            </Grid>
                                ABC 124
                            </Grid>
                        </Card>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            type="datetime-local"
                            id="startDate"
                            style={{ margin: "15px" }}
                            label="Fecha de inicio"
                            value={this.state.startDate}
                            onChange={this.handleChange} />
                    </Grid>
                    ¿Cuánto tiempo necesitas?
                    <Grid item xs={12} sm={12} md={12}>
                        <TextField
                            required
                            id="number"
                            type="number"
                            label="Número"
                            value={this.state.number}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>
                        <Grid item xs={12} sm={12} md={12}>
                            <FormControl >
                                <InputLabel style={{  marginLeft: "1em" }}>Días/Horas</InputLabel>
                                <Select
                                    value={this.state.age}
                                    onChange={this.handleChange}
                                    inputProps={{
                                        name: 'age',
                                        id: 'age-simple',
                                    }}
                                    style={{ width: "10em", margin: "1em" }}

                                >
                                    <MenuItem value={1}>Horas</MenuItem>
                                    <MenuItem value={24}>Días</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Button variant="raised"
                        onClick={this.handleSubmit}
                        style={{ margin: "15px", color: "#2f2e2e", backgroundColor: "#70f3c7" }}>
                        Alquilar
                        </Button>
                </Grid>
            </Card>
        )
    }
}

export default DateSelect;


