import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

class RegisterForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            lastName: '',
            ID: '',
            birthdate: '25-07-2017',
            phone: '',
            email: '',
            address: '',
            city: '',
            value: '',
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        console.log(event.target)
        this.setState({ [event.target.id]: event.target.value });
    }

    handleSubmit(event) {
        fetch('http://localhost:4000/'
            + this.state.name + '/'
            + this.state.lastName + '/'
            + this.state.email + '/'
            + this.state.phone,
            {
                method: 'GET',
                headers: { accept: 'application/json' }
            });
        /* .then((res) => {
            if (res.ok)
                return res.json();
        })
        .then((camara) => {
            this.setState({
                camara: camara
            });
        }); */
        event.preventDefault();
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <Grid container>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="name"
                            type="text"
                            label="Nombre"
                            value={this.state.name}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            type="text"
                            id="lastName"
                            label="Apellido"
                            style={{ margin: "15px" }}
                            value={this.state.lastName}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            id="ID"
                            required
                            type="text"
                            label="Cédula"
                            value={this.state.ID}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="email"
                            type="email"
                            label="Email"
                            value={this.state.email}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="phone"
                            type="number"
                            label="Celular"
                            value={this.state.phone}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            type="date"
                            id="birthdate"
                            label="Fecha de nacimiento"
                            style={{ margin: "15px" }}
                            value={this.state.birthdate}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            type="text"
                            id="address"
                            label="Dirección"
                            value={this.state.address}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="city"
                            type="text"
                            label="Ciudad"
                            value={this.state.city}
                            style={{ margin: "15px" }}
                            onChange={this.handleChange} />
                    </Grid>

                    <Grid item xs={12}>
                        <Button variant="raised"
                            onClick={this.handleSubmit}
                            style={{ margin: "15px", color: "#2f2e2e", backgroundColor: "#70f3c7" }}>
                            Enviar
                        </Button>
                    </Grid>
                </Grid>
            </form>
        );
    }
}

export default RegisterForm;


