import React, { Component } from 'react';

import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';

class CreditCardDummy extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <Grid container>
                <Grid item xs={12}>
                    <Card style={{ margin: "3em"}}>
                        <Grid container>
                            <Grid item xs={12}>
                                {this.props.name}
                            </Grid>
                            <Grid item xs={12}>
                                {this.props.number}
                            </Grid>
                            <Grid item xs={6}>
                                {this.props.expiry}
                            </Grid>
                            <Grid item xs={6}>
                                {this.props.issuer}
                            </Grid>
                            <Grid item xs={12}>
                                {this.props.cvv}
                            </Grid>
                        </Grid>
                    </Card>
                </Grid>
            </Grid>
        )
    }
}

export default CreditCardDummy;


