import React from 'react';

import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import Money from '@material-ui/icons/AttachMoney';
import Car from '@material-ui/icons/DirectionsCar';
import Calendar from '@material-ui/icons/DateRange';
import Profile from '@material-ui/icons/Portrait';

const styles = {
  root: {
    width: window.innerWidth,
  },
};

class SimpleBottomNavigation extends React.Component {
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };

  render() {
    const { classes } = this.props;
    const { value } = this.state;

    return (
      <BottomNavigation
        value={value}
        onChange={this.handleChange}
        showLabels
        className={classes.root}
      >
        <BottomNavigationAction label="Reservar" icon={<Calendar />} />
        <BottomNavigationAction label="Tu CarB" icon={<Car />} />        
        <BottomNavigationAction label="Promos" icon={<Money />} />
        <BottomNavigationAction label="Perfil" icon={<Profile />} />        
      </BottomNavigation>
    );
  }
}

SimpleBottomNavigation.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SimpleBottomNavigation);


