import React, { Component, Fragment } from 'react';

import CarCard from './CarCard';
import HEREMap from './HEREMap';
import CarCarousel from './CarCarousel';
import DateSelect from './DateSelect';

class Reserve extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            openCarCarousel: false,
            openDatePicker: false,            
            currentCar:0,
         }
    }

    onParkingClick() {
        this.setState({ openCarCarousel: true })
    }

    onNextCar(){
        this.setState({currentCar:this.state.currentCar+1})
    }

    onPrevCar(){
        this.setState({currentCar:this.state.currentCar-1})
    }
    onReserve(){
        this.setState({ openCarCarousel: false, openDatePicker:true })
    }

    render() {
        let locationArray = [
            [4.75443900, -74.04426900, "exito"],
            [4.73452800, -74.06544000, "exito"],
            [4.68374300, -74.08067700, "exito"],
            [4.63981500, -74.06572000, "exito"],
            [4.71922600, -74.03582500, "carulla"],
            [4.69589400, -74.04375900, "carulla"],
            [4.68743300, -74.05121800, "carulla"],
            [4.65537700, -74.05964400, "carulla"],
            [4.65246400, -74.05485000, "parking"],
        ]

        let carsArray = [
            ["RNT 504", "E+ 504", "Car B EV+", "Mitsubishi", "iMiev", "2012", "Automatic", "Blanco", "Electric", "FreeFlow", "True", "5"],
            ["NCR 775", "E+ 775", "Car B EV+", "Mitsubishi", "iMiev", "2013", "Automatic", "Blanco", "Electric", "FreeFlow", "True", "5"],
            ["NCR 792", "E+ 792", "Car B EV+", "Mitsubishi", "iMiev", "2013", "Automatic", "Blanco", "Electric", "FreeFlow", "True", "5"],
            ["NCR 780", "E+ 780", "Car B EV+", "Mitsubishi", "iMiev", "2014", "Automatic", "Blanco", "Electric", "FreeFlow", "True", "5"],
            ["NCR 779", "E+ 779", "Car B EV+", "Mitsubishi", "iMiev", "2014", "Automatic", "Blanco", "Electric", "FreeFlow", "True", "5"],
            ["NCR 781", "E+ 781", "Car B EV+", "Mitsubishi", "iMiev", "2014", "Automatic", "Blanco", "Electric", "FreeFlow", "False", "5"],
            ["NCR 786", "E+ 786", "Car B EV+", "Mitsubishi", "iMiev", "2014", "Automatic", "Blanco", "Electric", "FreeFlow", "False", "5"],
            ["NCR 782", "E+ 782", "Car B EV+", "Mitsubishi", "iMiev", "2014", "Automatic", "Blanco", "Electric", "FreeFlow", "False", "5"],
            ["EJR 867", "B 867 (Placa Impar)", "Car B Barcelona ", "Seat", "Leon", "2016", "Mechanic", "Gris claro", "Gasoline", "Restricted", "True", "5"],
            ["EJU 026", "B+ 026 (Placa par)", "Car B Barcelona ", "Seat", "Leon", "2016", "Automatic", "Gris ratón", "Gasoline", "Restricted", "False", "5"],
            ["DNL 421", "E 421 (Unipersonal)", "Car B EV", "Renault", "Twizy", "2016", "Automatic", "Negro", "Electric", "FreeFlow", "True", "1"],
            ["EJU 024", "B 024 (Placa par)", "Car B Barcelona +", "Seat", "Leon", "2016", "Mechanic", "Gris claro", "Electric", "Restricted", "False", "5"],
            ["JEX 084", "E 084 (Unipersonal)", "Car B EV", "Renault", "Twizy", "2016", "Automatic", "Negro", "Electric", "FreeFlow", "False", "1"],
            ["EIZ 521", "Ateca 521 (Placa Impar)", "Car B Ateca", "Seat", "Ateca", "2018", "Automatic", "Blanco", "Electric", "Restricted", "True", "5"]
        ]

        return (
            <Fragment>
                <HEREMap 
                locations={locationArray} 
                onClick={this.onParkingClick.bind(this)} />
                {this.state.openCarCarousel ?
                    <Fragment>
                        <CarCarousel 
                        cars={carsArray}
                        currentCar={this.state.currentCar}
                        clickNext={this.onNextCar.bind(this)}
                        clickBack={this.onPrevCar.bind(this)}
                        clickRent={this.onReserve.bind(this)}/>
                    </Fragment> : null}
                {this.state.openDatePicker ?
                    <Fragment>
                        <DateSelect />
                    </Fragment> : null}
            </Fragment>
        )
    }
}

export default Reserve;


