import React, { Component } from 'react';

import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

class CarCard extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        if (this.props.position == this.props.currentCar) {

            return (
                <Card style={{
                    width: window.innerWidth / 1.7,
                    textAlign: "center",
                    padding: "1em",
                    margin: "auto"
                }}>
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography variant="title" >
                                {this.props.carName}
                            </Typography>
                            <Card style={{
                                marginLeft: "3em",
                                marginRight: "3em",
                                height: "2em",
                                backgroundColor: "yellow"
                            }}>
                                <Typography variant="headline" gutterBottom>
                                    {this.props.plate}
                                </Typography>
                            </Card>
                        </Grid>
                        <Grid item xs={12}>
                            <img src="/car.png" height="75em" />
                        </Grid>
                        <Grid item xs={6}>
                            {this.props.transmission}
                        </Grid>
                        <Grid item xs={6}>
                            {this.props.fuelType}
                        </Grid>
                        <Grid item xs={6}>
                            {this.props.seats} Puestos
                            </Grid>
                        <Grid item xs={6}>
                            {this.props.fuelType == "Electric" ? "SIN Pico y Placa" : ""}
                        </Grid>
                        <Grid item xs={6}>
                            $23000/hora
                        </Grid>
                        <Grid item xs={6}>
                            $800/Km
                            </Grid>
                        <Grid item xs={12}>
                            <Button variant="raised"
                                onClick={this.props.clickRent}
                                style={{ margin: "15px", color: "#2f2e2e", backgroundColor: "#70f3c7" }}>
                                Alquilar
                        </Button>
                        </Grid>
                    </Grid>
                </Card>
            )
        }

        return null;

    }
}

export default CarCard;


