import React, { Component, Fragment } from 'react';

import CreditCardDummy from './CreditCardDummy';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

class RegisterForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            number: '',
            expiryDate: '31-12-1901',
            cvv: '',
            issuer: '',
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        console.log(event.target)
        this.setState({ [event.target.id]: event.target.value });
    }

    handleSubmit(event) {
        /*     fetch('http://localhost:4000'
                + '/' + this.state.name
                + '/' + this.state.number
                + '/' + this.state.email
                + '/' + this.state.phone,
                {
                    method: 'GET',
                    headers: { accept: 'application/json' }
                });
            /* .then((res) => {
                if (res.ok)
                    return res.json();
            })
            .then((camara) => {
                this.setState({
                    camara: camara
                });
            }); */
        event.preventDefault();
    }

    render() {
        return (
            <Fragment>
                <form onSubmit={this.handleSubmit}>
                    <Grid container>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="name"
                                type="text"
                                label="Nombre"
                                value={this.state.name}
                                style={{ margin: "15px" }}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="number"
                                type="number"
                                label="Número"
                                value={this.state.number}
                                style={{ margin: "15px" }}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                type="date"
                                id="expiryDate"
                                style={{ margin: "15px" }}
                                label="Fecha de Vencimiento"
                                value={this.state.expiryDate}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="issuer"
                                type="text"
                                label="issuer"
                                value={this.state.issuer}
                                style={{ margin: "15px" }}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="cvv"
                                required
                                type="text"
                                value={this.state.cvv}
                                style={{ margin: "15px" }}
                                label="Código de Seguridad"
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid item xs={12}>
                            <CreditCardDummy
                                cvv={this.state.cvv}
                                name={this.state.name}
                                issuer={this.state.issuer}
                                number={this.state.number}
                                expiry={this.state.expiryDate !== "31-12-1901" ? this.state.expiryDate : ""} />
                        </Grid>
                        <Grid item xs={12}>
                            <Button variant="raised"
                                onClick={this.handleSubmit}
                                style={{ margin: "15px", color: "#2f2e2e", backgroundColor: "#70f3c7" }} >
                                Enviar
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Fragment>
        );
    }
}

export default RegisterForm;


