import React, { Component } from 'react';

class HEREMap extends Component {
    constructor(props) {
        super(props);
        this.state = { reserve: {} }
    }

    addDomMarker(map) {
        let onClick = this.props.onClick;

        this.props.locations.forEach(element => {
            var outerElement = document.createElement('div'),
                innerElement = document.createElement('img');

            outerElement.style.userSelect = 'none';
            outerElement.style.webkitUserSelect = 'none';
            outerElement.style.msUserSelect = 'none';
            outerElement.style.mozUserSelect = 'none';
            outerElement.style.cursor = 'default';
            innerElement.style.marginTop = '-10px';
            innerElement.style.marginLeft = '-10px';
            innerElement.setAttribute('src', './pins/' + element[2] + 'on.png')
            innerElement.setAttribute('width', '30px')

            outerElement.appendChild(innerElement);

            function changeOpacity(evt) {
                evt.target.style.opacity = 0.6;
            };

            function changeOpacityToOne(evt) {
                evt.target.style.opacity = 1;
            };

            //create dom icon and add/remove opacity listeners
            var domIcon = new window.H.map.DomIcon(outerElement, {
                // the function is called every time marker enters the viewport
                onAttach: function (clonedElement, domIcon, domMarker) {
                    clonedElement.addEventListener('mouseover', changeOpacity);
                    clonedElement.addEventListener('mouseout', changeOpacityToOne);
                    clonedElement.addEventListener('click', onClick);
                    clonedElement.addEventListener('touchend', onClick);
                    
                },
                // the function is called every time marker leaves the viewport
                onDetach: function (clonedElement, domIcon, domMarker) {
                    clonedElement.removeEventListener('mouseover', changeOpacity);
                    clonedElement.removeEventListener('mouseout', changeOpacityToOne);
                },
            });
            let bearsMarker = new window.H.map.DomMarker(
                { lat: element[0], lng: element[1] }, {
                    icon: domIcon
                });
            map.addObject(bearsMarker);
        });
    }

    componentDidMount() {
        var platform = new window.H.service.Platform({
            'app_id': 'qR3gWlDjubYbxJFbkEfQ',
            'app_code': 'alinIrlPWgrA-6AUGeRL4g'
        });
        var pixelRatio = window.devicePixelRatio || 1;
        var defaultLayers = platform.createDefaultLayers({
            tileSize: pixelRatio === 1 ? 256 : 512,
            ppi: pixelRatio === 1 ? undefined : 320
        });
        var map = new window.H.Map(
            document.getElementById('mapContainer'),
            defaultLayers.normal.map,
            {
                zoom: 12,
                center: { lat: 4.682868, lng: -74.071318 },
                pixelRatio: pixelRatio,
            });
        var behavior = new window.H.mapevents.Behavior(new window.H.mapevents.MapEvents(map));
        var ui = window.H.ui.UI.createDefault(map, defaultLayers, 'es-ES');

        this.addDomMarker(map);
    }

    render() {
        return (
            <div id="mapContainer" style={{ height: window.innerHeight - 112 }}>
            </div>
        )
    }
}

export default HEREMap;


